.video-section {
    
    &__container {
        border-top: 1px solid $border-color;
        padding: 240px 0;
    }

    &__item {
        width: 702px;
        margin: 0 auto;
    }

    &__title {
        font-size: 48px;
        line-height: 1.21;
        text-align: center;    
        color: $text-color;
        margin-bottom: 13px;
    }

    &__text {
        font-size: 21px;
        line-height: 1.43;
        text-align: center;    
        color: $text-color-secondary;
        margin-bottom: 32px;
    }
}

.progress-list {
    display: flex;
    justify-content: center;
    gap: 90px;
    margin-bottom: 40px;

    &__item {
        display: flex;
        gap: 16px;
        align-items: center;
    }

    // &__icon {
    //     stroke: drop-shadow(0px 4px 10px rgba(20, 20, 43, 0.04));
    // }

    &__title {
        font-weight: 400;
        font-size: 16px;
        line-height: 1.13;
        letter-spacing: 1.6px;
        text-transform: uppercase;    
        color: $text-color-secondary;
        margin-bottom: 4px;
    }

    &__number {
        font-weight: 700;
        font-size: 28px;
        line-height: 1.29;
        color: $text-color;
    }
}
