.f-video-container {
    position: relative;
    display: block;
    margin: 0 auto;
    width: 1012px;
    height: 570px;
}
.f-video-player {
    position: relative;
    overflow: hidden;
    height: 0;
    padding-bottom: 56.25%;
    border-radius: 53px;
}

.f-video-player iframe,
.f-video-player object,
.f-video-player embed {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.f-video-cover {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
}